import Vue from "vue";
import { io } from "socket.io-client";

const eventBus = new Vue();

export default eventBus;

export const state = Vue.observable({
  connected: false,
  QueueMemberAgent: [],
  ExceededLimit: [],
  abandonedCalls: [],
  Metrics: new Map(),
  CustomMetrics: [],
});

let origin = location.origin;

origin = origin?.replace(/^http(s)?:\/\//, "wss://");
const hasPort = origin?.match(/:\d+$/);

if (hasPort) {
  origin = origin?.replace(/:\d+$/, ":3333");
} else {
  origin += ":3333";
}

const URL = origin;
console.log("URL: " + origin);

export const socket = io(URL);

socket.on("connect", () => {
  state.connected = true;
  // console.log('Connected')
  socket.emit("Buffer", "send buffer agent info");
  socket.emit("Buffer2", "send buffer Agentes em Pausa Excedida");
  socket.emit("Buffer3", "send buffer abandonedCalls");
  socket.emit("Buffer4", "send buffer Metrics");
  socket.emit("Buffer5", "send buffer CustomMetrics");
});

socket.on("disconnect", () => {
  state.connected = false;
  // console.log('Disconnected')
});
socket.on("Buffer", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('Buffer', state.QueueMemberAgent)
});
socket.on("Buffer2", (...args) => {
  state.ExceededLimit = new Map(Object.entries(JSON.parse(args)));
  // console.log('Buffer2', state.ExceededLimit)
});

socket.on("Buffer3", (...args) => {
  state.abandonedCalls = new Map(Object.entries(JSON.parse(args)));
  // console.log('Buffer3', state.abandonedCalls)
});

socket.on("Buffer4", (...args) => {
  state.Metrics = new Map(Object.entries(JSON.parse(args)));
  // console.log('Buffer4', state.Metrics)
});

socket.on("Buffer5", (...args) => {
  state.CustomMetrics = new Map(Object.entries(JSON.parse(args)));
  // console.log('Buffer5', state.CustomMetrics)
});

socket.on("QueueMemberPause", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('QueueMemberPause', state.QueueMemberAgent)
});

socket.on("ExceededLimit", (...args) => {
  state.ExceededLimit = new Map(Object.entries(JSON.parse(args)));
  // console.log('ExceededLimit', state.ExceededLimit)
});

socket.on("abandonedCalls", (...args) => {
  state.abandonedCalls = new Map(Object.entries(JSON.parse(args)));
  // console.log('abandonedCalls', state.abandonedCalls)
});

socket.on("QueueMemberAdded", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('QueueMemberAdded', state.QueueMemberAgent)
});

socket.on("DialBegin", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('DialBegin', state.QueueMemberAgent)
});

socket.on("BridgeEnter", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('BridgeEnter', state.QueueMemberAgent)
});

socket.on("Hangup", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('Hangup', state.QueueMemberAgent)
});

socket.on("DialEnd", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('DialEnd', state.QueueMemberAgent)
});

socket.on("QueueMemberRemoved", (...args) => {
  state.QueueMemberAgent = new Map(Object.entries(JSON.parse(args)));
  // console.log('QueueMemberRemoved', state.QueueMemberAgent)
});

socket.on("CustomMetrics", (...args) => {
  state.CustomMetrics = new Map(Object.entries(JSON.parse(args)));
  // console.log('CustomMetrics', state.CustomMetrics)
});

socket.on("Metrics", (...args) => {
  state.Metrics = new Map(Object.entries(JSON.parse(args)));
  // console.log('Metrics', state.Metrics)
});

